@import url(https://fonts.googleapis.com/css?family=Baloo+Tamma+2);
@import url(https://fonts.googleapis.com/css?family=Baloo+Tamma+2);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
form {
  display: inline-block;
  font-family: 'Baloo Tamma 2', cursive;
}
.form {
    display: inline-block;
    font-family: 'Baloo Tamma 2', cursive;
  }
  .card {
    /* display: inline-block; */
    font-family: 'Baloo Tamma 2', cursive;
  }
.overlay {
    z-index: -1;
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: auto;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.5); /*can be anything, of course*/
  }
  
  .bg-agoda {
    background-color: #2084ba!important;
  }
  
  .navbrand-logo {
    display: inline-block;
    position: absolute;
    top: 0;
    bottom: 0;
    padding: 0.6rem 15px;
    background: #fff;
  }
  .navbrand-logo img {
    height: 2rem;
    width: auto;
    vertical-align: center;
  }
  .logo-bottom-curve {
    border-bottom: 1px solid rgba(0,0,0,.25);
    position: absolute;
    right: 0;
    bottom: -10px;
    left: 0;
    height: 20px;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    background: #fff;
    box-shadow: 0 1px 0 rgba(0,0,0,.1), inset 0 -1px 1px 0 rgba(0,0,0,.05);
    z-index: -1;
  }
  
  .v-div {
    height: 3rem;
    width: 1px;
    background-color: #cacaca;
  }
  
  .App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .fade-in {
    opacity: 1;
    animation-name: fadeInOpacity;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-duration: 0.5s;
  }
  @keyframes fadeInOpacity {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
